        transition: top .3s;
        background-color: #00A14B;
        color: white;
        top: 70px;
    }
    
    .dropdown-list-active {
        position: absolute;
        display: -webkit-flex;
        display: flex;
        -webkit-flex-direction: column;
                flex-direction: column;
        transition: top .3s;
        background-color: #00A14B;
        color: white;
        opacity: 1;
        top: 50px;
        visibility: visible;
        padding-left: 10px;
        padding-right: 50px;
        padding-bottom: 10px;
    }

